<template>
    <v-dialog v-model="showDialog" width="500">
        <v-form ref="form" v-model="valid">
            <v-card class="v-card--outlined v-sheet--tile mt-4 pa-4">
                <v-row>
                    <v-col>
                        <h5><span v-if="isEditMode">Edit</span><span v-else>Add</span> Staff Member</h5>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-select
                            key="text-department"
                            v-model="AffiliateStaff.StaffDepartmentId"
                            :items="departments"
                            item-title="StaffDepartmentName"
                            item-value="StaffDepartmentId"
                            :rules="[rules.required]"
                        >
                            <template #label>
                                <span v-if="rules.required" class="text-red">* </span>Department
                            </template>
                        </v-select>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-text-field v-model="AffiliateStaff.StaffTitle" :rules="[rules.required]">
                            <template #label>
                                <span v-if="rules.required" class="text-red">* </span>Title
                            </template>
                        </v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <IndividualAutocomplete
                            v-model="AffiliateStaff.IndividualId"
                            :individual="AffiliateStaff.Individual"
                            :rules="[rules.required]"
                            :affiliateId="this.affiliateId"
                            filter="current"
                        />
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12" lg="6">
                        <div class="data-container">
                            <v-menu
                                v-model="pickerStartDate"
                                :close-on-content-click="false"
                                :offset="40"
                                transition="scale-transition"
                                min-width="290px"
                            >
                                <template v-slot:activator="{ props }">
                                    <v-text-field
                                        v-model="AffiliateStaff.TermStartDate"
                                        hint="YYYY-MM-DD"
                                        v-bind="props"
                                        :rules="[rules.required]"
                                    >
                                        <template #label>
                                            <span v-if="rules.required" class="text-red">* </span>Start Date:
                                        </template>
                                    </v-text-field>
                                </template>
                                <v-date-picker v-model="AffiliateStaff.TermStartDate" @input="pickerStartDate = false"/>
                            </v-menu>
                        </div>
                    </v-col>
                    <v-col cols="12" lg="6">
                        <div class="data-container">
                            <v-menu
                                v-model="pickerEndDate"
                                :close-on-content-click="false"
                                :offset="40"
                                transition="scale-transition"
                                min-width="290px"
                            >
                                <template v-slot:activator="{ props }">
                                    <v-text-field
                                        v-model="AffiliateStaff.TermEndDate"
                                        hint="YYYY-MM-DD"
                                        label="End Date:"
                                        v-bind="props"
                                    />
                                </template>
                                <v-date-picker v-model="AffiliateStaff.TermEndDate" @input="pickerEndDate = false"/>
                            </v-menu>
                        </div>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12" lg="4"/>
                    <v-col>
                        <v-btn elevation="0" color="default" class="mb-4 btn-block" v-on:click="cancel()">
                            Cancel
                        </v-btn>
                    </v-col>
                    <v-col>
                        <v-btn elevation="0" color="primary" class="mb-4 btn-block" v-on:click="save()">
                            Save
                        </v-btn>
                    </v-col>
                </v-row>
            </v-card>
        </v-form>
    </v-dialog>
</template>

<script>
    import { clone } from "lodash-es";
    import IndividualAutocomplete from "./IndividualAutocomplete";

    export default {
        name: "AffiliateStaffEditorComponent",
        components: {IndividualAutocomplete},
        props: {
            isEditMode: {
                type: Boolean,
                default: false
            },
            editedStaff: {
                type: Object,
                required: false
            },
            departments: {
                required: true,
                type: Array
            },
            affiliateId: {
                required: true,
                type: Number
            }
        },
        data: () => ({
            showDialog: true,
            pickerStartDate: false,
            pickerEndDate: false,
            AffiliateStaff: {},
            rules: {
                required: value => !!value || 'Required.'
            },
            valid: true
        }),
        beforeMount() {
            if (this.editedStaff) {
                this.AffiliateStaff = clone(this.editedStaff);
                this.AffiliateStaff.StaffDepartmentId = this.AffiliateStaff.StaffDepartment.StaffDepartmentId;
                this.AffiliateStaff.IndividualId = this.AffiliateStaff.Individual.IndividualId;
                this.AffiliateStaff.TermStartDate = this.AffiliateStaff.TermStartDate.split('T')[0];
                this.AffiliateStaff.TermEndDate = this.AffiliateStaff.TermEndDate.split('T')[0];
            }
        },
        watch: {
            editedStaff: {
                handler(data) {
                    this.AffiliateStaff = clone(this.editedStaff);
                },
                deep: true
            }
        },
        methods: {
            cancel() {
                this.AffiliateStaff = {};
                this.$emit('canceled');
            },
            save() {
                if (!this.$refs.form.validate()) {
                    return;
                }

                const AffiliateStaff = {
                    ...this.AffiliateStaff,
                    AffiliateId: this.affiliateId
                };
                let method = 'post';
                let url = '/api/v2/affiliateStaff';
                if (this.isEditMode) {
                    method = 'put';
                    url += '/' + AffiliateStaff.AffiliateStaffId;
                }
                let saved = null;
                axios({method, url, data: AffiliateStaff})
                    .then((response) => {
                        saved = response.data;
                    })
                    .finally(() => {
                        this.$emit('saved', saved);
                    });
            },

        }
    }
</script>
