<template>
    <v-dialog v-model="showDialog"
              width="500">
        <v-form ref="form" v-model="valid">
            <v-card>
                <v-card-title><span v-if="isEditMode">Edit</span><span v-else>Add</span> Officer Role</v-card-title>
                <v-card-text>
                    <v-text-field
                        key="text-role-name"
                        v-model="role.AffiliateOfficerRoleName"
                        :rules="[rules.required]"
                        variant="underlined"
                    >
                        <template #label>
                            <span v-if="rules.required" class="text-red">* </span>Name
                        </template>
                    </v-text-field>
                    <v-select
                        v-model="OfficerRoleTypeId"
                        :items="roleTypes"
                        item-value="OfficerRoleTypeId"
                        item-title="OfficerRoleTypeName"
                        @update:model-value="filterTitles()"
                        :rules="[rules.required]"
                        variant="underlined"
                    >
                        <template #label>
                            <span v-if="rules.required" class="text-red">* </span>Type
                        </template>
                    </v-select>
                    <v-select
                        v-model="role.OfficerRoleTitleId"
                        item-title="OfficerRoleTitleName"
                        item-value="OfficerRoleTitleId"
                        :items="filteredTitles"
                        :rules="[rules.required]"
                        variant="underlined"
                    >
                        <template #label>
                            <span v-if="rules.required" class="text-red">* </span>Title
                        </template>
                    </v-select>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn elevation="0" color="primary" class="mb-4 btn-block" v-on:click="cancel()">
                        Cancel
                    </v-btn>
                    <v-btn elevation="0" color="primary" class="mb-4 btn-block" v-on:click="save()">
                        Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-form>
    </v-dialog>
</template>

<script>

    export default {
        name: "RoleEditorComponent",
        props: {
            affiliateId: {
                type: Number,
                required: true
            },
            isEditMode: {
                type: Boolean,
                default: false
            },
            roleTypes: {
                type: Array
            },
            roleTitles: {
                type: Array
            },
            role: {
                type: Object,
                default: function () {
                    return {
                        AffiliateOfficerRoleId: 0,
                        AffiliateOfficerRoleName: '',
                        OfficerRoleTitleId: ''
                    }
                }
            }
        },
        data: () => ({
            OfficerRoleTypeId: '',
            filteredTitles: [],
            showDialog: true,
            rules: {
                required: value => !!value || 'Required.'
            },
            valid: true
        }),
        methods: {
            save() {
                if (!this.$refs.form.validate()) {
                    return;
                }
                let method = 'post';
                let url = '/api/v2/affiliateOfficerRole';
                const role = {
                    ...this.role,
                    AffiliateId: this.affiliateId
                };
                if (this.isEditMode) {
                    method = 'put';
                    url += '/' + this.role.AffiliateOfficerRoleId;
                }
                // url += '?include=Affiliate,OfficerRoleTitle';
                if (role.AffiliateOfficerRoleId === 0) {
                    delete role.AffiliateOfficerRoleId;
                }
                axios({method, url, data: role})
                    .then((response) => {
                    })
                    .finally((response) => {
                        this.$emit('saved');
                    });

            },
            cancel() {
                this.$emit('canceled');
            },
            filterTitles() {
                this.filteredTitles = this.roleTitles.filter((v => v.OfficerRoleTypeId === this.OfficerRoleTypeId));
            },
            getTypeFromTitle() {
                if (this.role.OfficerRoleTitleId !== '') {
                    const filtered = this.roleTitles.filter(v => v.OfficerRoleTitleId === this.role.OfficerRoleTitleId);
                    if (filtered && filtered.length > 0) {
                        return filtered[0].OfficerRoleTypeId;
                    }
                }
                return '';
            }
        },
        beforeMount() {
            this.OfficerRoleTypeId = this.getTypeFromTitle();
            this.filterTitles();
        }
    }

</script>
