<template>
    <v-dialog v-model="showDialog"
              width="500">
        <v-form ref="form" v-model="valid">
            <v-card>
                <v-card-title><span v-if="isEditMode">Edit</span><span v-else>Add</span> Person to Role</v-card-title>
                <v-card-text>
                    <v-row>
                        <v-col>
                            <v-select
                                v-model="affiliateOfficer.AffiliateOfficerRoleId"
                                item-title="AffiliateOfficerRoleName"
                                item-value="AffiliateOfficerRoleId"
                                :items="roles"
                                :affiliateId="affiliateId"
                                :rules="[rules.required]"
                                variant="underlined"
                            >
                                <template #label>
                                    <span v-if="rules.required" class="text-red">* </span>Role
                                </template>
                            </v-select>
                            <IndividualAutocomplete
                                v-model="affiliateOfficer.IndividualId"
                                :individual="affiliateOfficer"
                                :rules="[rules.required]"
                                :affiliateId="affiliateId"
                                filter="member"
                            />
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="12" lg="6">
                            <div class="data-container">
                                <v-menu
                                    v-model="pickerStartDate"
                                    :close-on-content-click="false"
                                    :offset="40"
                                    transition="scale-transition"
                                    min-width="290px"
                                >
                                    <template v-slot:activator="{ props }">
                                        <v-text-field
                                            v-model="termStartDate"
                                            hint="YYYY-MM-DD"
                                            label="Start Date:"
                                            v-bind="props"
                                            variant="underlined"
                                        />
                                    </template>
                                    <v-date-picker v-model="termStartDate" @input="pickerStartDate = false"/>
                                </v-menu>
                            </div>
                        </v-col>
                        <v-col cols="12" lg="6">
                            <div class="data-container">
                                <v-menu
                                    v-model="pickerEndDate"
                                    :close-on-content-click="false"
                                    :offset="40"
                                    transition="scale-transition"
                                    min-width="290px"
                                >
                                    <template v-slot:activator="{ props }">
                                        <v-text-field
                                            v-model="termEndDate"
                                            hint="YYYY-MM-DD"
                                            label="End Date:"
                                            v-bind="props"
                                            variant="underlined"
                                        />
                                    </template>
                                    <v-date-picker v-model="termEndDate" @input="pickerEndDate = false"/>
                                </v-menu>
                            </div>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col>
                            <v-checkbox key="text-is-elected" label="Elected Position" v-model="isElectedCheckbox"/>
                        </v-col>
                    </v-row>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="default" variant="elevated" v-on:click="cancel()">
                        Cancel
                    </v-btn>
                    <v-btn elevation="0" color="primary" variant="elevated" v-on:click="save()">
                        Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-form>
    </v-dialog>
</template>

<script>

    import IndividualAutocomplete from "./IndividualAutocomplete";
    export default {
        name: "RolePersonEditorComponent",
        components: {IndividualAutocomplete},
        props: {
            isEditMode: {
                type: Boolean,
                default: false
            },
            roles: {
                type: Array
            },
            affiliateOfficer: {
                type: Object,
                default: function () {
                    return {
                        AffiliateOfficerId: 0,
                        AffiliateOfficerRoleId: '',
                        IndividualId: '',
                        TermStartDate: '',
                        TermEndDate: '',
                        IsElected: false,
                        IsCurrent: true,
                        IsPreferredTitle: false,
                        AffiliateId: 0
                    }
                }
            },
            affiliateId: {
                required: true
            },
            individuals: {
                type: Object | Array,
                default: '',
                required: false
            },
        },
        data: () => ({
            pickerStartDate: false,
            pickerEndDate: false,
            showDialog: true,
            rules: {
                required: value => !!value || 'Required.'
            },
            valid: true,
            isElectedCheckbox: false,
            termEndDate: '',
            termStartDate: ''
        }),
        methods: {
            save() {
                if (this.individuals) {
                    // check if the individual is already assigned to the same role without an end date
                    const allIndividuals = Object.values(this.individuals).flat();
                    const isDuplicateWithoutEndDate = allIndividuals.some(individual => {
                        return individual.Id == this.affiliateOfficer.IndividualId &&
                            individual.AffiliateOfficerRoleId == this.affiliateOfficer.AffiliateOfficerRoleId &&
                            !individual.TermEndDate;
                    });

                    if (isDuplicateWithoutEndDate) {
                        alert('This individual is already assigned to this role without an end date.');
                        return;
                    }
                }

                if (!this.$refs.form.validate()) {
                    return;
                }
                let method = 'post';
                let url = '/api/v2/affiliateOfficer';

                this.affiliateOfficer.IsElected = this.isElectedCheckbox ? 1 : 0;
                // sync watched variables
                this.affiliateOfficer.TermEndDate = this.termEndDate;
                this.affiliateOfficer.TermStartDate = this.termStartDate;

                const affiliateOfficer = {
                    ...this.affiliateOfficer,
                    AffiliateId: this.affiliateId
                };

                if (this.isEditMode) {
                    method = 'put';
                    url += '/' + this.affiliateOfficer.AffiliateOfficerId;
                }
                if (affiliateOfficer.AffiliateOfficerId === 0) {
                    delete affiliateOfficer.AffiliateOfficerId;
                }
                axios({method, url, data: affiliateOfficer})
                    .then((response) => {
                    })
                    .finally(() => {
                        this.$emit('saved');
                    });
            },
            cancel() {
                this.$emit('canceled');
            }
        },
        beforeMount() {
            if (this.isEditMode) {
                // we got Individual id flattened in Id.
                this.affiliateOfficer.AffiliateOfficerRoleId = Number(this.affiliateOfficer.AffiliateOfficerRoleId);
                this.affiliateOfficer.IndividualId = Number(this.affiliateOfficer.Id);
                this.isElectedCheckbox = this.affiliateOfficer.IsElected == 1;
            }
        },
        watch: {
            'affiliateOfficer.IsElected': {
                immediate: true,
                handler(newVal) {
                    this.isElectedCheckbox = newVal === 1;
                },
            },
            'affiliateOfficer.TermEndDate': {
                immediate: true,
                handler(newVal) {
                    this.termEndDate = newVal || '';
                },
            },
            'affiliateOfficer.TermStartDate': {
                immediate: true,
                handler(newVal) {
                    this.termStartDate = newVal || '';
                },
            },
        },
    }
</script>
