<template>
    <div>
        <v-menu
            open-on-hover
        >
            <template v-slot:activator="{ props }">
                <v-btn rounded="lg" color="primary" v-bind="props" icon="mdi:mdi-cloud-download" class="download-button" />
            </template>
            <v-list>
                <v-list-item
                    v-for="(item, index) in downloadTypes"
                    :key="index"
                    :value="item.value"
                    :disabled="item.disabled"
                    @click="download(item)"
                >
                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item>
            </v-list>
        </v-menu>
        <v-dialog v-model="downloading" persistent max-width="320">
            <v-card>
                <v-card-title class="text-h5">Downloading...</v-card-title>
                <v-card-text class="pb-0">
                  <v-progress-linear
                      :indeterminate="downloadRecordTotal === 0"
                      :model-value="(downloadRecordCount * 100) / downloadRecordTotal"
                  ></v-progress-linear>
                    <!-- <p class="mb-0">{{ downloadPercent }}% : {{ downloadRecordCount }} of {{ downloadRecordTotal }} records received...</p> -->
                    <!-- <p v-if="downloadSize > 0" class="mb-0 small">{{ downloadSize }} bytes received...</p> -->
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn @click="downloadCancel()">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
export default {
    name: "DownloadComponent",
    props: {
        downloadBaseURL: {
            type: String
        },
        downloadAllUrlParams: {
            type: String,
            default: ''
        },
        downloadUrlParams: {
            type: String
        },
        configurationModel: {
            type: String
        },
        configurationKey: {
            type: String
        },
        include: {
            type: String,
            default: ''
        },
        useHomeAddress: {
            type: Boolean,
            default: false
        },
    },
    data: () => ({
        downloadURL: null,
        downloading: false,
        downloadingCancelToken: null,
        downloadTypes: [
            // { title: 'Export List Below (Excel)', format: 'xlsx', type: 'filtered', disabled: true },
            // { title: 'Export All Records and Fields (Excel)', format: 'xlsx', type: 'all', disabled: true },
            { title: 'Export List Below (CSV)', format: 'csv', type: 'filtered', disabled: false },
            { title: 'Export All Records and Fields (CSV)', format: 'csv', type: 'all', disabled: false },
        ],
        downloadRecordCount: 0,
        downloadRecordTotal: 0,
        downloadRecordChunk: 0,
        downloadPercent: 0,
        downloadSize: 'calculating',
    }),
    methods: {
        download(options) {
            let self = this;
            let url = this.downloadBaseURL + '/download/' + options.format + '/' + options.type + '?model=' + this.configurationModel + '&key=' + this.configurationKey + '&' + this.include;
            url += '&useHomeAddress=' + this.useHomeAddress;
            console.log(url);
            if (options.type === 'all') {
                url += '&' + this.downloadAllUrlParams;
            }
            else {
                url += '&' + this.downloadUrlParams;
            }
            this.downloading = true;

            this.downloadingCancelToken = axios.CancelToken.source();

            return axios({
                url: url,
                method: 'GET',
                responseType: 'blob',
                cancelToken: this.downloadingCancelToken.token,
                onDownloadProgress: function(event) {
                    self.downloadRecordTotal = parseInt(event.target.getResponseHeader('Content-Record-Count'));
                    self.downloadRecordChunk = parseInt(event.target.getResponseHeader('Content-Record-Chunk'));
                    self.downloadSize = event.loaded;
                    self.downloadRecordCount = self.downloadRecordCount + self.downloadRecordChunk;
                    if (self.downloadRecordCount >= self.downloadRecordTotal) {
                        self.downloadRecordCount = self.downloadRecordTotal;
                        self.downloadPercent = 100;
                    } else {
                        self.downloadPercent = Math.round((self.downloadRecordCount * 100.0) / self.downloadRecordTotal);
                    }
                    console.log(self.downloadRecordCount + ' of ' + self.downloadRecordTotal);
                    console.log('downloaded ' + event.loaded);
                },
            })
                .then(response => {
                    const fileURL = window.URL.createObjectURL(new Blob([response.data]));
                    const fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    let filename = 'download.csv';
                    const disposition = response.headers['content-disposition'];
                    if (disposition && disposition.indexOf('attachment') !== -1) {
                        const filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;
                        const matches = filenameRegex.exec(disposition);
                        if (matches != null && matches[1]) {
                            filename = matches[1].replace(/['"]/g, '');
                        }
                    }
                    fileLink.setAttribute('download', filename);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                    fileLink.remove();
                })
                .catch(error => {
                    if (axios.isCancel(error)) {
                        // do nothing
                    } else {
                        console.log(error);
                    }
                    this.downloadingCancelToken = null;
                    this.downloading = false;
                })
                .finally(() => {
                    this.downloadingCancelToken = null;
                    this.downloading = false;
                    this.downloadSize = 'calculating';
                    this.downloadRecordCount = 0;
                    this.downloadRecordTotal = 0;
                    this.downloadPercent = 0;
                });
        },
        downloadCancel() {
            if (this.downloadingCancelToken) {
                this.downloadingCancelToken.cancel();
                this.downloadingCancelToken = null;
            }
            this.loading = false;
            this.downloading = false;
        }
    },
}
</script>

