<template>
    <v-dialog v-model="visible" width="500">
        <v-card class="v-card--outlined v-sheet--tile mt-4 pa-4">
            <v-row>
                <v-col cols="12">
                    <h5>In order to edit this individual's {{ componentName.toLowerCase() }} role, you will be directed to the Affiliate Record for [{{ affiliateName }}] [{{ affiliateNumber }}] to access the Manage {{ componentName }} Roles form.</h5>
                </v-col>
            </v-row>
            <v-row>
                <v-col></v-col>
                <v-col>
                    <v-btn size="small" color="default"  @click="visible=false">Cancel</v-btn>
                </v-col>
                <v-col>
                    <v-btn size="small" color="primary"  @click="doSwitch()">OK</v-btn>
                </v-col>
            </v-row>
        </v-card>
    </v-dialog>
    <v-btn size="small" class="affiliate-switch"  @click="visible=true">Manage on Affiliate Record</v-btn>
</template>
<script>
    export default {
        name: 'AffiliateSwitchRolesComponent',
        props: {
            componentName: {
                required: true
            },
            sectionName: {
                required: false
            },
            affiliateName: {},
            affiliateNumber: {},
        },
        data: () => ({
            visible: false,
        }),
        methods: {
            doSwitch() {
                this.visible = false;
                this.$router.push('/affiliate#' + this.sectionName);
            }
        }
    }
</script>
