<template>
    <v-autocomplete
        :items="stateTerritories"
        :item-value="itemValue"
        :item-title="itemText"
        v-model="model"
        :rules="rules"
    >
        <template #label>
            <span v-if="rules" class="text-red">* </span>{{label}}
        </template>
    </v-autocomplete>
</template>

<script>
    import emptySelectMixin from "../../mixins/UI/emptySelectMixin";

    export default {
        name: "StateTerritorySelectComponent2",
        mixins: [emptySelectMixin],
        props: {
            label: {
                default: 'State'
            },
            itemValue: {
                default: 'StateTerritoryId'
            },
            itemText: {
                default: 'StateTerritoryName'
            },
            value: {
                required: true
            },
            rules: {
                required: false
            },
            stateTerritories: {
                required : true
            }
        },
        computed: {
            model: {
                get() {
                    return this.value;
                },
                set(newValue) {
                    this.$emit('input', newValue);
                }
            }
        }
    }
</script>
